.promo {
  background-image: url("../img/promo-bgr.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding: 60px 0;
  border-bottom: 5px solid @main;
}

.promo-name {
  margin: 0;
  font-size: 40px;
}

.promo-img {
  border-radius: 50%;
  height: 300px;
  object-fit: cover;
  border: 3px dotted @main;
}

.promo-link {
  font-size: 14px;
  color: rgba(118, 129, 140, 0.5);
  border-bottom: 1px dashed rgba(118, 129, 140, 0.5);
}

.promo-work {
  font-size: 20px;
  margin: 0;
  color: @main;
  letter-spacing: 3px;
}

.promo-text {
  background-color: rgba(250, 250, 250);
  display: inline-block;
  margin: -50px;
  padding: 10px 20px;
}
