@font-face {
  font-family: "SFUIText";
  font-weight: 400;
  font-style: normal;
  src: local("SFUIText"),
    url("../fonts/SFUIText-Regular.woff2") format("woff2"),
    url("../fonts/SFUIText-Regular.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  font-family: "SFUITex", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: @grey;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  padding: 0 15px;
  max-width: 1360px;
  margin: 0 auto;
}


@media (min-width: @tablet-width) {
  .container {
    padding: 0 30px;
  }
}

@media (min-width: @desktop-width) {
  .container {
    padding: 0 125px;
    max-width: 1360px;
    margin: 0 auto;
  }
}
