.works-part {
  display: flex;
  margin: 20px 0;
  flex-direction: column;
}

.works-title {
  font-size: 30px;
  text-align: center;
  padding: 10px 10px;
  font-weight: 400;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.works-img {
  &:hover {
    transform: scale(0.95);
  }
}

.works-button {
  border: dotted 1px @main;
  padding: 15px 10px;
  border-radius: 10px;
  display: block;
  text-align: center;
  color: @main;
  margin-bottom: 10px;

  &:hover {
    color: #ffffff;
    background-color: @main;
    border: none;
  }
}


.works-name {
  font-size: 18px;
  margin: 0;
  color: @main;
  text-align: center;
  font-weight: 600;
}

.works-about {
  flex: 0 0 100px;
}

.works-description {
  margin-bottom: 50px;
  font-size: 16px;
}

@media (min-width: @tablet-width) {
  .works-about {
    flex: 0 0 300px;
    margin-left: 60px;
  }

  .works-part {
    flex-direction: row;
  }

  .works-button {
    display: inline-block;
    color: @main;
    border: dotted 1px @main;
    padding: 15px;
    font-size: 18px;
    margin: 0;
  }

  .works-description {
    padding: 0;
  }

  .works-name {
    text-align: left;
    font-weight: 500;
  }
}

@media (min-width: @desktop-width) {
  .works-about {
    flex: 0 0 500px;
  }
}
