@font-face {
  font-family: "SFUIText";
  font-weight: 400;
  font-style: normal;
  src: local("SFUIText"), url("../fonts/SFUIText-Regular.woff2") format("woff2"), url("../fonts/SFUIText-Regular.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0 auto;
  font-family: "SFUITex", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #76818D;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  padding: 0 15px;
  max-width: 1360px;
  margin: 0 auto;
}
@media (min-width: 900px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    padding: 0 125px;
    max-width: 1360px;
    margin: 0 auto;
  }
}
.promo {
  background-image: url("../img/promo-bgr.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding: 60px 0;
  border-bottom: 5px solid #1abc9c;
}
.promo-name {
  margin: 0;
  font-size: 40px;
}
.promo-img {
  border-radius: 50%;
  height: 300px;
  object-fit: cover;
  border: 3px dotted #1abc9c;
}
.promo-link {
  font-size: 14px;
  color: rgba(118, 129, 140, 0.5);
  border-bottom: 1px dashed rgba(118, 129, 140, 0.5);
}
.promo-work {
  font-size: 20px;
  margin: 0;
  color: #1abc9c;
  letter-spacing: 3px;
}
.promo-text {
  background-color: rgba(250, 250, 250);
  display: inline-block;
  margin: -50px;
  padding: 10px 20px;
}
.works-part {
  display: flex;
  margin: 20px 0;
  flex-direction: column;
}
.works-title {
  font-size: 30px;
  text-align: center;
  padding: 10px 10px;
  font-weight: 400;
  letter-spacing: 3px;
  text-transform: uppercase;
}
.works-img:hover {
  transform: scale(0.95);
}
.works-button {
  border: dotted 1px #1abc9c;
  padding: 15px 10px;
  border-radius: 10px;
  display: block;
  text-align: center;
  color: #1abc9c;
  margin-bottom: 10px;
}
.works-button:hover {
  color: #ffffff;
  background-color: #1abc9c;
  border: none;
}
.works-name {
  font-size: 18px;
  margin: 0;
  color: #1abc9c;
  text-align: center;
  font-weight: 600;
}
.works-about {
  flex: 0 0 100px;
}
.works-description {
  margin-bottom: 50px;
  font-size: 16px;
}
@media (min-width: 900px) {
  .works-about {
    flex: 0 0 300px;
    margin-left: 60px;
  }
  .works-part {
    flex-direction: row;
  }
  .works-button {
    display: inline-block;
    color: #1abc9c;
    border: dotted 1px #1abc9c;
    padding: 15px;
    font-size: 18px;
    margin: 0;
  }
  .works-description {
    padding: 0;
  }
  .works-name {
    text-align: left;
    font-weight: 500;
  }
}
@media (min-width: 1300px) {
  .works-about {
    flex: 0 0 500px;
  }
}
.page-footer__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.page-footer {
  background-color: rgba(118, 129, 140, 0.2);
  padding: 40px 0;
  border-top: 5px solid #1abc9c;
}
.contacts-list__link {
  color: #76818D;
}
.contacts-list__link:hover {
  color: #1abc9c;
}
.contacts-list__item {
  margin: 5px 0;
}
